// CUSTOM CSS

// Responsive sizing (changes font size, but also margins, padding, etc... due to use of rem)
html {
  font-size: 1rem;
}

@include media-breakpoint-down(sm) {
  html {
    font-size: 0.9rem;
  }
}

@include media-breakpoint-down(xs) {
  html {
    font-size: 0.8rem;
  }
}

body {
  font-size: 1.125rem;
}

.math-var {
  font-family: $font-family-math;
  font-style: italic;
}

// CLEAN
.sdt-page {
  @include make-container();
}

.sdt-layout {
  @include make-row();
}

.sdt-sidebar {
  @include make-col-ready();
  @include make-col(10, 10);

  order: 0;

  border-bottom: $border;

  @include media-breakpoint-up(md) {
    @include make-col(2, 10);

    min-height: 100vh;

    border-right: $border;

    @supports (position: sticky) or (position: -webkit-sticky) {
      position: -webkit-sticky; /* stylelint-disable-line value-no-vendor-prefix */
      position: sticky;
      top: 0;
      z-index: 1000;

      height: 100vh;
    }
  }

  @include media-breakpoint-up(lg) {
    @include make-col(2, 12);
  }

  @include media-breakpoint-up(xl) {
    flex: 0 1 20rem;
  }

  .sdt-detectable {
    font-size: 1.5rem;
  }
}

.sdt-brandnav {
  position: relative;
  display: flex;

  align-items: center;

  padding: 0.5rem;
  margin-right: ($grid-gutter-width / -2);
  margin-left: ($grid-gutter-width / -2);
}

.sdt-tocnav-toggle {
  @extend .btn;
  @extend .btn-link;

  padding: 0.1875rem 0.5rem;
  margin: 0;

  line-height: 1;
  color: $body-color;

  border: 0;

  @include media-breakpoint-up(md) {
    display: none;
  }

  &:hover {
    @include shadow(2);

    color: $body-color;
  }

  &:focus {
    @include shadow(2);

    color: $body-color;
  }

  &:active {
    @include shadow(4);

    color: $body-color;
  }

  svg {
    width: 1.875rem;
    height: 1.875rem;
  }
}

.sdt-brand-link {
  @extend .navbar-brand;

  padding: 0 0.5rem;
  margin: 0;

  font-weight: 600;
  color: $body-color;

  border-radius: 0.25rem;
  outline: none;

  &:hover {
    @include shadow(2);

    color: $body-color;
    text-decoration: none;
  }

  &:focus {
    @include shadow(2);

    color: $body-color;
    text-decoration: none;
  }

  &:active {
    @include shadow(4);

    color: $body-color;
    text-decoration: none;
  }
}

.sdt-tocnav {
  padding-top: 0.25rem;
  padding-bottom: 0.5rem;
  margin-right: ($grid-gutter-width / -2);
  margin-left: ($grid-gutter-width / -2);

  @include media-breakpoint-up(md) {
    @supports (position: sticky) {
      max-height: calc(100vh - 3.5rem);
      overflow-y: auto;
    }
  }
  // Override collapse behaviors
  @include media-breakpoint-up(md) {
    display: block !important;
  }
}

.sdt-toc {
  @extend .nav;

  display: block;
}

.sdt-toclink {
  padding: 0.375rem 0.5rem;

  font-size: 1rem;
  font-weight: 600;
  line-height: 1.1;
  color: $link-color;

  border-radius: 0.25rem;
  outline: none;

  &:hover {
    @include shadow(2);

    color: $body-color;
    text-decoration: none;
  }

  &:focus {
    @include shadow(2);

    color: $body-color;
    text-decoration: none;
  }

  &:active {
    @include shadow(4);

    color: $body-color;
    text-decoration: none;
  }

}

.sdt-tocitem .sdt-toclink {
  display: block;

  padding: 0.375rem 0.5rem;
  margin: 0 0.5rem;
}

.sdt-tocitem-active .sdt-toclink,
.sdt-tocitem-active:hover .sdt-toclink {
  font-weight: 700;
  color: $body-color;
}

.sdt-tocitem-extra.sdt-tocitem-1 {
  padding-top: 1.25rem;
}

.sdt-main {
  @include make-col-ready();
  @include make-col(10, 10);

  padding-top: 1rem;
  padding-bottom: 1rem;

  @include media-breakpoint-up(md) {
    @include make-col(8, 10);
  }

  @include media-breakpoint-up(lg) {
    @include make-col(10, 12);
  }

  @include media-breakpoint-up(xl) {
    @include make-col(8, 12);
    border-right: $border;
  }
}

.sdt-content {
  padding-bottom: 1rem;

  // STYLING FOR MARKDOWN
  h2 {
    padding-top: 1rem;
  }
}

.sdt-footer {
  @include make-row();

  padding-top: 1rem;

  border-top: $border;
}

.sdt-footer-item {
  @include make-col-ready();

  flex: 0 0 auto;

  width: auto;
  max-width: none;
  padding: 0 0.5rem;

  font-size: 1rem;
  font-weight: 600;
  line-height: 1.1;

  &.sdt-footer-item-left {
    margin-right: auto;
  }
}
